<template>
  <div id="app">
    <h2>Tic-Tac-Toe</h2>

    <div class="config">
      <label>
        Rows:
        <input v-model="rows" type="number">
      </label>
      <label>
        Cols:
        <input v-model="cols" type="number">
      </label>
      <label>
        Player cursor:
        <input @change="updateAi" v-model="cursor.player" placeholder="Player">
      </label>
      <label>
        AI Cursor:
        <input @change="updateAi" v-model="cursor.ai" placeholder="AI">
      </label>
      <label>
        Player:
        <select v-model="first_move">
          <option value="player">Player</option>
          <option value="ai" selected>AI</option>
        </select>
      </label>

      <button @click="start">Render</button>
    </div>

    <div :key="boardKey" class="board">
      <div class="row" v-for="(row, ri) in board">
        <div class="cell" v-for="(cell, ci) in row" track-by="cell" @click="cellClick(ri, ci)">
          <span class="cell-content">{{ renderCell(cell) }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import shortid from 'shortid';
  import { ttt } from '../../../ai-kido';

  export default {
    name: 'app',

    data() {
      const cursor = { player: 'X', ai: 'O' };

      return {
        first_move: 'player',
        boardKey: shortid(),
        cursor,
        ai: ttt({ player_cursor: cursor.player, ai_cursor: cursor.ai }),
        rows: 3,
        cols: 3,
        board: []
      };
    },

    methods: {
      start() {
        this.board = new Array(parseInt(this.rows, 10))
          .fill(null)
          .map(it => new Array(parseInt(this.cols, 10)).fill(null));

        if (this.first_move === 'ai') setTimeout(() => this.aiMove(), 300);
      },

      updateAi() {
        this.ai = ttt({ player_cursor: this.cursor.player, ai_cursor: this.cursor.ai });
      },

      aiMove() {
        const [x, y] = this.ai(this.board);

        this.board[y][x] = this.cursor.ai;

        this.boardKey = shortid();
      },

      cellClick(ri, ci) {
        if (this.board[ri][ci]) return;

        this.board[ri][ci] = this.cursor.player;

        this.boardKey = shortid();

        setTimeout(() => this.aiMove(), 300);
      },

      renderCell(cell) {
        return cell && `${cell}`;
      }
    }
  };
</script>

<style>
  #app {
    text-align: center;
    margin-top: 60px;
    flex: 1;
  }
  body {
    background-color: #9E9E9E;
  }
  label {
    color: white;
    margin: 5px;
  }
  h2 {
    color: white;
    font-weight: bold;
  }
  select {
    outline: none;
    border: none;
    width: 80px;
    height: 20px;
    border-radius: 2px;
    padding-left: 15px;
  }
  input {
    outline: none;
    border: none;
    width: 30px;
    height: 20px;
    border-radius: 2px;
    padding-left: 15px;
  }
  button {
    margin-top: 10px;
    margin-left: 10px;
    border: none;
    outline: none;
    background-color: #818181;
    border-radius: 2px;
    height: 30px;
    width: 80px;
    color: #d3d3d3;
  }
  button:active {
    filter: blur(1px);
  }
  .config {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    margin-right: 48%;
  }
  .board {
    margin-top: 50px;
    flex: 1;
    justify-content: center;
  }
  .row {
    flex-direction: row;
  }
  .cell {
    position: relative;
    display: inline-block;
    width: 100px;
    height: 100px;
    background-color: #FFC107;
    border-radius: 2px;
    margin: 0 2px 0 2px;
  }
  .cell:hover {
    filter: blur(1px)
  }
  .cell-content {
    color: white;
    font-family: "Comic Sans MS";
    font-size: 70px;
    font-weight: bold;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
</style>



